.App {
  text-align: center;
  font-family: 'Quicksand', sans-serif;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 60px;
}

.big{
  font-size: 1.4em;
  display: inline-block;
}

.App-header {
  background-image: -webkit-radial-gradient( circle farthest-corner at -4% -12.9%,  rgba(74,98,110,1) 0.3%, rgba(30,33,48,1) 90.2% );
  background-image: -o-radial-gradient( circle farthest-corner at -4% -12.9%,  rgba(74,98,110,1) 0.3%, rgba(30,33,48,1) 90.2% );
  background-image: radial-gradient( circle farthest-corner at -4% -12.9%,  rgba(74,98,110,1) 0.3%, rgba(30,33,48,1) 90.2% );
  height: 65px;
  padding: 20px;
  font-size: 1.5em;
  font-weight: bolder;
  color: white;
  text-align: left;
}

.App-title {
  font-size: 1.5em;
  letter-spacing: 0.15em;
  display: inline-block;
}

.App-intro {
  font-size: large;
}

.m
{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  /* color: white; */
}

td{
  width: 15%;
  padding-bottom: .25em;
}

.container{
  padding-right: 10px;
  padding-left: 10px;
  margin-right: auto;
  margin-left: auto;
}

.bigcontainer{
  margin-bottom: 30px;
}

.device_details
{
  margin-left: auto !important;
  margin-right: auto !important;
  margin: 10px 0px 20px 0px;
  width: 21%;
  height: auto;
  font-size: 1.1em;
  font-weight: 700;
  padding: 0px 22px 23px 22px;
  text-align: center;
  -ms-flex-direction: column;
      flex-direction: column;
  border-radius: 4px;
  border-width: 0.5px;
  border-style: solid;
  border-color: #CCCCCC;
  -webkit-box-shadow: 0px 15px 7px -7px #00323D;
          box-shadow: 0px 15px 7px -7px #00323D;
}

@media only screen and (max-width : 1366px) {
  .device_details
  {
    margin: 20px 5px 5px 5px;
    width: 28%;
    height: auto;
    font-size: 0.87em;
    font-weight: 700;
    padding:  0px 20px 20px 20px;
    /* color: white; */
    text-align: center;
    -ms-flex-direction: column;
        flex-direction: column;
    border-radius: 7px;
    border-color: #BBB;
    border-width: thin;
  }
  .device_details:hover{
    /* height: ; */
  }
  .container{
    padding-right: 5px;
    padding-left: 5px;
    margin-right: auto;
    margin-left: auto;
  }
  .container1{
    padding-right: 0px;
    padding-left: 200px;
    margin-right: auto;
    margin-left: 0%;
  }
  .App-header {
    background-image: -webkit-radial-gradient( circle farthest-corner at -4% -12.9%,  rgba(74,98,110,1) 0.3%, rgba(30,33,48,1) 90.2% );
    background-image: -o-radial-gradient( circle farthest-corner at -4% -12.9%,  rgba(74,98,110,1) 0.3%, rgba(30,33,48,1) 90.2% );
    background-image: radial-gradient( circle farthest-corner at -4% -12.9%,  rgba(74,98,110,1) 0.3%, rgba(30,33,48,1) 90.2% );
    height: 45px;
    padding: 20px;
    font-size: 1em;
    font-weight: bolder;
    color: white;
    text-align: left;
  }
}
.child{
  /* display: inline-block; */
  float: right;
}
