@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

p{
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: left;
  letter-spacing: 0.13em;
  font-weight: 500;
}

.main{
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  margin-top: 1.2em;
  margin-bottom: 10px;
  text-align: left;
  letter-spacing: 0.13em;
  font-weight: 500;
}

.first{
  font-size: 1.5em;
  font-weight: bold;
  display: inline-block;
  position: relative;
  /* float: left; */
}

.second{
  font-weight: 500;
  display: inline-block;
  font-size:1.1em;
}

.third{
  font-weight: bold;
  font-size: 1.1em;
  text-align: left;
}

.fourth{
  display: inline-block;
  float: left;
  width: 50%;
  text-align: left;
  font-size: 1.1em;
  padding-top: 0.8em;
  letter-spacing: 0.05em;
}

.gradient_background{
  background-color: #21D4FD;
  background-image: -webkit-linear-gradient(289deg, #21D4FD 0%, #B721FF 100%);
  background-image: -o-linear-gradient(289deg, #21D4FD 0%, #B721FF 100%);
  background-image: -webkit-linear-gradient(71deg, #21D4FD 0%, #B721FF 100%);
  background-image: -o-linear-gradient(71deg, #21D4FD 0%, #B721FF 100%);
  background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);
}

.G6-A, .G6-B, .G6-C, .G6-D, .G6-E, .G6-F, .G6-G, .G6-H, .G6-I, .G6-J, .G6-K, .G6-L {
  height:15px;
  width: auto;
  background-image: -webkit-linear-gradient(top left, #FF9D6C 10%, #BB4E75 100%);
  background-image: -o-linear-gradient(top left, #FF9D6C 10%, #BB4E75 100%);
  background-image: -webkit-gradient(linear, left top, right bottom, color-stop(10%, #FF9D6C), to(#BB4E75));
  background-image: linear-gradient(to bottom right, #FF9D6C 10%, #BB4E75 100%);
}

.G7-A, .G7-B, .G7-C, .G7-D, .G7-E, .G7-F, .G7-G, .G7-H, .G7-I, .G7-J, .G7-K, .G7-L{
  height:15px;
  width: auto;
  background-image: -webkit-linear-gradient( 340.4deg,  rgba(0,158,218,1) 14.2%, rgba(5,104,174,1) 84% );
  background-image: -o-linear-gradient( 340.4deg,  rgba(0,158,218,1) 14.2%, rgba(5,104,174,1) 84% );
  background-image: linear-gradient( 109.6deg,  rgba(0,158,218,1) 14.2%, rgba(5,104,174,1) 84% );
}

.G8-A, .G8-B, .G8-C, .G8-D, .G8-E, .G8-F, .G8-G, .G8-H, .G8-I, .G8-J, .G8-K, .G8-L{
  height:15px;
  width: auto;
  background-image: -webkit-linear-gradient(left, #0a7300, #319504, #53b707, #76db06, #9cff00);
  background-image: -o-linear-gradient(left, #0a7300, #319504, #53b707, #76db06, #9cff00);
  background-image: -webkit-gradient(linear, left top, right top, from(#0a7300), color-stop(#319504), color-stop(#53b707), color-stop(#76db06), to(#9cff00));
  background-image: linear-gradient(to right, #0a7300, #319504, #53b707, #76db06, #9cff00);
}

.headalef
{
  height:10px;
  width: auto;
  background-color: black;
}

.title{
  margin-top: 0.9em;
  margin-left: 1em;
  margin-bottom: 1em;
  padding: 0.3em;
  font-size: 2em;
  font-weight: 800;
  text-align: left;
  letter-spacing: 0.1em;
  border-bottom: 5px solid #8C8C8A;
}

.bordertop{
  border-top: 1px solid #8C8C8A;
}

.footer {
  position: relative;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
  background-color: #eFEFEF;
  text-align: center;
}



